*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    border-style: none;
}

.mainCoin{
    background-color: #F8F6F4;
    width: 100vw;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.coins{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #C4DFDF;
    color: whitesmoke;
    padding: 8px 24px 8px 24px;
    border-style: solid;
    border-width: 1px;
    border-color: rgba(0, 0, 0, 0.137);
    font-size: 0.8rem;
    min-width: 1000px;
}
.coins div {
    margin: 8px 24px 8px 24px;
}
.coins div:nth-child(2){
    margin-left: 0;
}
.coins img {
    max-width: 50px;
    margin: 0;
}

input{
    background-color: red;
}

.mainCard{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}